body {
  background-color: #6b9fe8;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

label.logo {
  color: #fff;
  float: left;
  padding-top: 2%;
  padding-left: 1%;
  font-size: 35px;
}

.topnav {
  background-color: #444;
  overflow: hidden;
}

.topnav ul {
  float: right;
}

.topnav ul li {
  text-align: center;
  background-color: #3289d5;
  padding: 14px 16px;
  font-size: 17px;
  text-decoration: none;
  display: inline-block;
}

.topnav ul li a {
  color: #2f2f2f;
  background-color: #8d2323;
  border-width: 25px 10px 4px 35px;
}

.topnav a:hover {
  color: #000;
  background-color: #ddd;
}

.topnav a.active {
  color: #fff;
  background-color: #04aa6d;
  border-width: 500px;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    background-color: #60786f;
    display: block;
    transform: scale(1.3, 2.43);
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive .icon {
    position: absolute;
    top: 0;
    right: 0;
  }

  .topnav.responsive a {
    float: none;
    text-align: left;
    display: block;
  }
}

#task-grid {
  grid-template: "taskform tasklist" 1fr
  / 1fr 1fr;
  display: grid;
}

#taskform {
  max-width: 300px;
  grid-area: taskform;
  margin: 0 auto;
}

#taskform label {
  width: 100%;
  display: block;
}

#tasklist {
  grid-area: tasklist;
  list-style-type: none;
}

