label.logo{
    color:white;
    font-size: 35px;
    float: left;
    padding-top: 2%;
    padding-left: 1%;
}


.topnav {
    overflow: hidden;
    background-color: rgb(68, 68, 68);
  }
  
.topnav ul{
    float:right;
}
  .topnav ul li{
    display: inline-block;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    background-color:rgb(50, 137, 213);
  }

  .topnav ul li a{
      color:rgb(47, 47, 47);
      border-width: 25px 10px 4px 35px;
      
      background-color: rgb(141, 35, 35);
  }
  
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .topnav a.active {
    background-color: #04AA6D;
    color: white;
    border-width: 500px;
  }

.topnav .icon {
    display: none;
}

@media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {display: none;}
    .topnav a.icon {
        background-color: #60786f;
        float: right;
        transform: scale(1.3,2.43);
        display:block;
    }
}

@media screen and (max-width: 600px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive .icon {
        position: absolute;
        right: 0;
        top: 0;
    }
    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
    }
}